import './App.css';
import AEMHeadless from '@adobe/aem-headless-client-js'

function App() {

  const AEM_HOST_URI = 'https://publish-p35809-e67526-cmstg.adobeaemcloud.com' 
  const AEM_USER = "admin"
  const AEM_PASS = "admin"

  const aemHeadlessClient = new AEMHeadless({
    serviceURL: AEM_HOST_URI,
    endpoint: 'content/graphql/endpoint.gql',
    auth: [AEM_USER, AEM_PASS]
  })

  const execQuery = () => {
    console.log('Executing query')
    aemHeadlessClient.listPersistedQueries()
      .then(data => console.log(data))
      .catch(e => console.error(e.toJSON()))
  }

  return (
    <div className="App">
      <header className="App-header">
      <button onClick={execQuery}>Execute GraphQL</button>
      </header>
    </div>
  );
}

export default App;
